body {
  font-family: 'sans-serif';
  background: -webkit-gradient(linear, left top, right top, from(rgba(226, 216, 226, 0.5)), 
  to(rgb(122, 117, 117)));
  background: -webkit-linear-gradient(left, rgba(226, 216, 226, 0.5), 
  rgb(122, 117, 117));
  background: -o-linear-gradient(left, rgba(226, 216, 226, 0.5), 
  rgb(122, 117, 117));
  background: linear-gradient(to right, rgba(226, 216, 226, 0.5), 
  rgb(122, 117, 117));
}

#submit {
  color: #fff;
  font-size: 2em;
  background: #AD1457;
  border: 0;
  padding: 0 10px;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}


#main h1{
  color: #444;
  text-align: center;
}

#main{
  padding: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#book-list{
  padding: 0;
}

#book-list li{
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #880E4F;
  -webkit-box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
          box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
  cursor: pointer;
  color: #880E4F;
}

form{
  background: #fff;
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 400px;
}

.form-fileds {
  border: 1px solid black;
}

form .field{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

form label{
  text-align: right;
  padding: 6px;
}

form input, form select{
  margin: 4px 0;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

form button{
  color: #fff;
  font-size: 2em;
  background: #AD1457;
  border: 0;
  padding: 0 10px;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}

#book-details{
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  background: #AD1457;
  padding: 30px;
  overflow: auto;
  -webkit-box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
          box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff;
}



